.tariffs {
  $block-name:                &; // #{$block-name}__element

  @include block-margin;
  position: relative;
  //overflow: hidden; // не активен, иначе обрежет анимашку

  // @media (min-width: $screen-xxl) {
  //   overflow: hidden;
  // }

  &__header {
    margin-bottom: 24px;

    @media (min-width: $screen-xl) {
      text-align: center;
    }
  }

  &__header-col {
    @include col(xs, 12);
    @include col(md, 12);
    @include col-offset(xl, 2);
    @include col(xl, 8);
  }

  // &__title {

  // }

  &__intro {

    p {
      hyphens: none;
      text-wrap: pretty;
      color: var(--text-pale-color);
    }
  }

  &__slider-wrapper {

    &--short {
      //overflow: hidden;
      @include gradient-border-mask(
        2px,
        $border-radius--32,
        var(--corner-gradient-blue-mod),
        transparent
      )

      @media (max-width: $screen-xl - 1) {
        padding-right: 0;
        border-top-right-radius: 0 !important;
        border-bottom-right-radius: 0 !important;

        &::before {
          border-top-right-radius: 0 !important;
          border-bottom-right-radius: 0 !important;
        }
      }

      // &#{$block-name}__slider-wrapper--dark {
      // }

      // &#{$block-name}__slider-wrapper--light {
      // }
    }
  }

  &__controls {
    position: relative;
    z-index: 10;
    margin-bottom: 24px;
  }

  &__select-locations {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
    position: relative;
    max-width: 1000px;
    margin-right: auto;
    margin-left: auto;

    & > * {
      flex: 1 1;
      flex-basis: calc(50% - 12px);
      min-width: 300px;
    }
  }

  &__select-filters {
    display: flex;
    flex-wrap: wrap;
    gap: 24px 40px;
    margin-top: 24px;

    @media (min-width: $screen-xl) {
      justify-content: center;
    }

    .field-toggler,
    .field-filter__title {
      margin-bottom: 0;
    }
  }

  &__slider {
    display: flex;
    align-items: flex-start;
    justify-content: initial;
    padding-right: 0 !important;

    // &:not(.tariffs__slider--short) {
    //   padding-right: 0 !important;
    // }

    &--short {

      @media (max-width: $screen-xl - 1) {
        padding-right: 0 !important;
      }

      .tariffs__slide-caption,
      .tariff-card__header {
        height: 92px;
      }

      .tariff-card__footer {
        padding-bottom: 24px;
      }

      #{$block-name}__slide-header {
        border-radius: 0;

        &::before {
          display: none !important;
        }
      }

      #{$block-name}__slider-content {
        border-top-color: transparent;
      }

      // &#{$block-name}__slider--dark {}

      // #{$block-name}__slider--light {}
    }

    // &--dark { }

    &--light {

      #{$block-name}__slide-header {

        &::before {

          background: linear-gradient(135deg,
                          var(--blue-500),
                          rgba(39, 125, 255, 0.24) 15%,
                          var(--blue-150) 50%,
                          transparent 60%);

          @media (min-width: $screen-md) {

            background: linear-gradient(140deg,
                          var(--blue-500),
                          rgba(39, 125, 255, 0.24) 25%,
                          var(--blue-150) 40%,
                          transparent 74%);
          }
        }

        // @media (min-width: $screen-md) {

        //   &::before {
        //   }
        // }
      }

      #{$block-name}__header-item {

        &::after {
          background: linear-gradient(to right, transparent, var(--opacity-blue-40));
        }
      }

      #{$block-name}__slider-content {
        border-top-color: var(--blue-150);
      }
    }
  }

  &__slide-header {

    @include gradient-border-mask(
      2px,
      24px,
      linear-gradient(130deg, var(--blue-500),
                              var(--opacity-white-12) 10%,
                              transparent 100%),
                              rgba(0, 0, 0, 0)
    )

    @media (min-width: $screen-md) {
      flex: 0 1;
      flex-basis: 330px;
      max-width: 330px;
      min-width: 300px;
      padding-left: 40px;
      padding-bottom: 24px;

      @include gradient-border-mask(
        2px,
        64px,
        linear-gradient(135deg, var(--blue-500), var(--opacity-white-12) 20%, transparent 80%),
        rgba(0, 0, 0, 0)
      )
    }

    &::before {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;

      @media (min-width: $screen-md) {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        //border-bottom-left-radius: 0;
      }
    }
  }

  &__slide-caption {
    display: flex;
    align-items: flex-end;
    height: 80px;
    padding: 16px 16px 16px 2px;
    margin-bottom: 0;

    @media (min-width: $screen-md) {
      height: 106px;
    }

    &--header {

      span {

        @media (max-width: $screen-md - 1) {
          @include visually-hidden;
        }
      }
    }
  }

  // &__header-list {

  // }

  &__header-item {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0 12px;
    position: relative;
    min-width: 40px;
    height: 40px;
    font-size: $font-size;
    font-weight: 500;

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      right: 0;
      left: 0;
      height: 1px;
      background: linear-gradient(to right, transparent, var(--opacity-white-16));
    }

    @media (min-width: $screen-md) {
      justify-content: initial;
      height: 56px;
      padding-right: 24px;
    }

    svg {
      flex-basis: 24px;
      width: 24px;
      height: 24px;
      fill: var(--blue-500);
    }

    span {
      display: none;

      @media (min-width: $screen-md) {
        display: block;
        white-space: nowrap;
      }
    }
  }

  &__slider-content {
    margin: 0 !important;
    border-top: 2px solid var(--opacity-white-12);

    @media (min-width: $screen-md) {
      flex: 0 1 auto;
    }

    // &--short {
    //   border-right: 2px solid var(--opacity-white-12);
    //   border-top-left-radius: 24px;
    // }
  }

  &__slider-controls {
    margin-top: 24px;

    @media (max-width: $screen-md - 1) {
      @include visibility-hidden;
    }
  }

  &__all {
    width: 240px;
    margin-top: 24px;

    @media (max-width: $screen-md - 1) {
      @include visibility-hidden;
    }
  }

  // анимация
  .lottie-player {
    position: absolute;
    z-index: -1;
    right: 0;
    left: 0;
    bottom: 50px;
    width: 100%;
    max-width: 100%;
    max-height: 100%;
    //aspect-ratio: 21 / 9;
    filter: blur(50px);

    @media (min-width: $screen-md) {
      bottom: 170px;
      filter: blur(80px);
    }

    @media (min-width: $screen-xl) {
      bottom: -130px;
      filter: blur(110px);
    }

    @media (min-width: $screen-xxl) {
      bottom: -180px;
      //aspect-ratio: 16 / 10;
      filter: blur(130px);
    }
  }

  &--short {
    //overflow-x: hidden;

    @media (max-width: $screen-xl - 1) {
      padding-right: 0;
    }

    .tariffs__header > .row {

      @media (max-width: $screen-xl - 1) {
        margin-right: 0;
      }
    }
  }
}
